import api from './url.js';
import request from '@/utils/request.js'
export default {
	superVList(params){	//v
		return request.post(api.superVList,params)
	},
	isNovaItem(itemId){	//v
		return request.post(api.isNovaItem+"?itemId="+itemId,{})
	},
	memberAchievement(){	//
		return request.post(api.memberAchievement,{})
	}
}