//   
export default {
	data(){
		return{
			isScopedFlag: false, //  
			authorizeFlag: false, //
		}	
	},
	mounted(){

		this.uniGetSetting();

	},
	methods:{
		previewImage() { // base64
			if(this.shareImage.indexOf('http')>-1){
				uni.downloadFile({
					url: this.shareImage,
					success: (res) => {
						if (res.statusCode == 200) {
							uni.previewImage({
								urls: [res.tempFilePath],
								longPressActions: {
									itemList: ['', '', ''],
									success: function(data) {},
									fail: function(err) {}
								}
							})
						}
					},
					fail: (err) => {}
				})
			}else{
				let base64 = this.shareImage.replace(/^data:image\/\w+;base64,/, ""); //data:image/png;base64,
				let filePath = wx.env.USER_DATA_PATH + '/hym_pay_qrcode.png';
				uni.getFileSystemManager().writeFile({
					filePath: filePath, //
					data: base64, //
					encoding: 'base64', //
					success: res => {
						uni.previewImage({
							urls: [filePath],
							longPressActions: {
								itemList: ['', '', ''],
								success: function(data) {},
								fail: function(err) {}
							}
						})
					},
					fail:err=>{
					}
				})
			}
		},
		previewDownLoad() { //
			this.isScopedFlag = false;
			this.getDownLoadScope();
		},
		getDownLoadScope() { //
			if (!this.authorizeFlag) {
				uni.authorize({
					scope: "scope.writePhotosAlbum",
					success: (res) => {
						this.downloadFile()
					},
					fail: (err) => {
						if (!this.isScopedFlag) {
							uni.showModal({
								title: "",
								content: "",
								confirm: () => {
									this.getDownLoadScope()
								},
								cancel: () => {
									uni.showToast({
										title: "",
										icon: "none"
									})
								}
							})
						}
						this.isScopedFlag = true;
		
					}
				})
			} else {
				this.downloadFile()
			}
		},
		downloadFile() {
			if(this.shareImage.indexOf('http')>-1){
				uni.downloadFile({
					url:this.shareImage,
					success:(res)=>{
						if(res.statusCode==200){
							uni.saveImageToPhotosAlbum({
								filePath:res.tempFilePath,
								success:(data)=>{
									uni.showToast({
										title:"",
										icon:"none"
									})
								},
								fail:(err)=>{
									uni.showToast({
										title:"",
										icon:"none"
									})
								}
							})
						}else{
							uni.showToast({
								title:"",
								icon:"none"
							})
						}
					}
				})
			}else{
				let base64 = this.shareImage.replace(/^data:image\/\w+;base64,/, ""); //data:image/png;base64,
				let filePath = wx.env.USER_DATA_PATH + '/hym_pay_qrcode.png';
				uni.getFileSystemManager().writeFile({
					filePath: filePath, //
					data: base64, //
					encoding: 'base64', //
					success: res => {
						uni.saveImageToPhotosAlbum({
							filePath: filePath,
							success: (data) => {
								uni.showToast({
									title: "",
									icon: "none"
								})
							},
							fail: (err) => {
								uni.showToast({
									title:"",
									icon:"none"
								})
							}
						})
					},
					fail: err => {
						uni.showToast({
							title:"",
							icon:"none"
						})
					}
				})
			}
		},
		uniGetSetting() { //
			let _this = this;
			uni.getSetting({
				success(res) {
					if (!res.authSetting['scope.writePhotosAlbum']) {
						_this.authorizeFlag = false
					} else {
						_this.authorizeFlag = false
					}
				},
				fail(err) {}
			})
		},
	}
}