import api from './url.js';
import request from '@/utils/request.js'
export default {
	provices(){	//
		return request.post(api.provices,{})
	},
	cities(id){	//
		return request.post(api.cities+id,{})
	},
	counties(id){	//
		return request.post(api.counties+id,{})
	},
	towns(id){	//
		return request.post(api.towns+id,{})
	}
}