
import FMAgent from '@/static/fmsdk-1.5.8/fm-1.5.8-es.min.js'

export default{
	data(){
		return{
			hideFMCanvas:true
		}
	},
	methods:{
		setData(obj){

			let that = this;    
			let keys = [];    
			let val,data;    
			Object.keys(obj).forEach(function(key){    
				keys = key.split('.');    
				val = obj[key];    
				data = that.$data;    
				keys.forEach(function(key2,index){    
					if(index+1 == keys.length){    
						that.$set(data,key2,val);    
					}else{    
						if(!data[key2]){    
							that.$set(data,key2,{});    
						}    
					}    
					data = data[key2];    
				})    
			});    

		},
		fmagentInit(){

				let _this = this;
				let _fmOpt = {
					partnerCode: "beifa", // partner code
					appName: "beifa_xcx", // app name
				}
				if(this.$config.addImageUrl=="https://api.beifamall.com"){
					_fmOpt.env = "PRODUCTION"
				}
				let fmagent = new FMAgent(_fmOpt);
				fmagent.getInfo({
					page: _this, // FMAgentPageComponent
					openid: '', // openid
					// unionidunionid
					// 
					unionid: '', 
					success: function (res) {
						uni.setStorageSync('fmagent',res)
					// resblackbox
					},
					fail: function (res) {
					// resexception
					},
					complete: function (res) {
					}
				})

		}
	}
}